import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { BehaviorSubject, combineLatest, Observable, ReplaySubject } from 'rxjs';
import { distinctUntilChanged, map } from 'rxjs/operators';
import { TranslateService } from '@ngx-translate/core';
import { filter as _filter } from 'lodash';
import {
  IFormBuilderEvent,
  IPlainObject,
  RxBundleCacheService,
  RxDefinitionNameService,
  RxGlobalCacheService,
  RxOverlayService,
} from '@helix/platform/shared/api';
import {
  IBreadcrumbItem,
  IValidationIssue,
  IValidationIssueSection,
  ValidationIssueType
} from '@helix/platform/ui-kit';
import { IWebApiDefinition, RxWebApiDefinitionService } from '@helix/platform/web-api/api';

@Component({
  selector: 'rx-web-api-designer',
  templateUrl: './web-api-designer.component.html',
  styleUrls: ['./web-api-designer.component.scss']
})
export class RxWebApiDesignerComponent implements OnInit {
  @Input()
  webApiDefinitionName: string;

  @Output()
  webApiDefinitionSaved: EventEmitter<string> = new EventEmitter();

  @Output()
  webApiDefinitionErrorLoading: EventEmitter<void> = new EventEmitter();

  @Output()
  closeDesigner: EventEmitter<void> = new EventEmitter<void>();

  isDesignMode = true;

  isExistingWebApi = false;

  isCustomizationEnabled = true;

  bundleFriendlyName$ = this.rxGlobalCacheService.getBundleFriendlyName(this.rxBundleCacheService.bundleId);

  validationIssues$ = new BehaviorSubject<IValidationIssueSection[]>([]);

  hasValidationErrors$: Observable<boolean> = this.validationIssues$.pipe(
    map((validationIssues) =>
      Boolean(
        _filter(validationIssues, {
          issues: [{ type: ValidationIssueType.Error }]
        }).length
      )
    ),
    distinctUntilChanged()
  );

  breadcrumbItems$: Observable<IBreadcrumbItem[]>;

  webApiDefinition$ = new BehaviorSubject<IWebApiDefinition>(null);

  isSaveButtonDisabled$: Observable<boolean>;

  isSavingInProgress$ = new BehaviorSubject<boolean>(false);

  private bundleId = this.rxBundleCacheService.bundleId;

  private isBundleEditable = this.rxOverlayService.isBundleEditable(this.bundleId);

  private destroyed$ = new ReplaySubject<boolean>(1);

  constructor(
    private rxGlobalCacheService: RxGlobalCacheService,
    private rxBundleCacheService: RxBundleCacheService,
    private rxWebApiDefinitionService: RxWebApiDefinitionService,
    private rxDefinitionNameService: RxDefinitionNameService,
    private translateService: TranslateService,
    private rxOverlayService: RxOverlayService
  ) {}

  ngOnInit(): void {
    if (this.webApiDefinitionName) {

    } else {

    }

    this.breadcrumbItems$ = this.webApiDefinition$.pipe(
      map((webApiDefinition) => {
        const definitionName = this.rxDefinitionNameService.getDisplayName(webApiDefinition?.name);

        return [
          {
            label: definitionName || `<${this.translateService.instant('com.bmc.arsys.rx.client.web-api-designer.new-web-api.label')}>`,
            data: {}
          }
        ];
      })
    );

    this.isSaveButtonDisabled$ = combineLatest([
      this.validationIssues$,
      this.isSavingInProgress$
    ]).pipe(
      map(([[validationIssueSection], isSavingInProgress]) =>
        Boolean(validationIssueSection?.issues) || !this.isBundleEditable || isSavingInProgress
      )
    );
  }

  canDeactivate(): boolean {
    return true;
  }

  toggleDesignMode(): void {
    this.isDesignMode = !this.isDesignMode;
  }

  onCorrectIssue(validationIssue: IValidationIssue): void {

  }

  validate(value: IPlainObject): IValidationIssueSection[] {
    const validationIssues: IValidationIssue[] = [];

    return validationIssues.length ? [{
      title: this.translateService.instant('com.bmc.arsys.rx.client.web-api-designer.new-web-api.label'),
      issues: validationIssues
    }] : [];
  }

  onRevertCustomization(event: IFormBuilderEvent): void {

  }

  saveDefinition(): void {

  }
}
